<template>
    <div>
        <da-manage-page>
            <div slot="button">
                <el-button icon="my-zengjia" @click="dialogVisible=true;">新增</el-button>
            </div>
            <el-form slot="search" :inline="true" :model="searchForm.formdata">
                <el-form-item label="分组名称">
                    <el-input v-model="searchForm.formdata.name"></el-input>
                </el-form-item>

                <el-form-item>
                    <el-button type="primary" @click="getTbList">查询</el-button>
                </el-form-item>
            </el-form>
            <da-table ref="table" slot="content" :colums="colsData" :table-list="tableList" :search-data="searchForm" @callBack="getTbList" :showSelection="false">

                <el-table-column property="operation" label="操作" width="150" slot="operation" align="center" fixed="right">
                    <template slot-scope="scope">
                        <el-button size="small" @click="editTb(scope.row)">编辑</el-button>
                        <el-button size="small" @click="delTb(scope.row)">删除</el-button>
                    </template>
                </el-table-column>

            </da-table>
        </da-manage-page>

        <da-dialog :title="title" :visible.sync="dialogVisible" @close="closeDialog('addForm')">
            <el-form label-width="90px" :model="addForm" :rules="rules" ref="addForm">

                <el-form-item label="分组名称" prop="name">
                    <el-input auto-complete="off" v-model="addForm.name"></el-input>
                </el-form-item>
            </el-form>

            <div slot="footer" class="dialog-footer">
                <el-button type="primary" @click="saveDialog('addForm')">确 定</el-button>
                <el-button @click="dialogVisible = false">取 消</el-button>
            </div>
        </da-dialog>
    </div>
</template>
<script>
export default {
  name: "alarmGroup",
  data() {
    return {
      // 查询表单
      searchForm: {
        formdata: { name: "" },
        currentPage: 1,
        pageSize: 20
      },
      tableList: {}, // 数据列表
      colsData: [
        // 表头
        { label: "分组名称", prop: "name", align: "left", sortable: true }
      ],
      dialogVisible: false,
      addForm: { name: "" },
      rules: this.eachValidate({ name: { required: true, maxString: "50" } })
    };
  },
  computed: {
    title() {
      //弹窗标题
      return this.addForm.id ? "编辑告警分组管理" : "新增告警分组管理";
    },
    urlType() {
      //URL请求方式
      return this.addForm.id ? "put" : "post";
    }
  },
  watch: {},
  methods: {
    getTbList() {
      let formName = "table";
      this.$ajax
        .get("rest/alarmGroup/page", {
          params: this.searchForm,
          loadingTarget: this.$refs[formName]
        })
        .then(res => {
          if (res.data.status) {
            this.tableList = res.data;
          }
        });
    },
    editTb(row) {
      this.dialogVisible = true;
      this.addForm = JSON.parse(JSON.stringify(row));
    },
    closeDialog(formName) {
      this.$nextTick(function() {
        this.$refs[formName].resetFields();
        this.addForm = { name: "" };
      });
    },
    saveDialog(formName) {
      this.$refs[formName].validate(valid => {
        if (valid) {
          this.$ajax[this.urlType]("rest/alarmGroup", this.addForm).then(
            res => {
              if (res.data.status) {
                this.$message.success(res.data.answerMsg);
                this.dialogVisible = false;
                this.getTbList();
              } else {
                this.$message.error(res.data.answerMsg);
              }
            }
          );
        }
      });
    },
    delTb(row) {
      this.$confirm("是否删除该记录?", "提示", {
        confirmButtonText: "确定",
        cancelButtonText: "取消",
        type: "warning"
      }).then(() => {
        this.$ajax
          .delete("rest/alarmGroup", { params: { id: row.id } })
          .then(res => {
            if (res.data.status) {
              this.$message.success(res.data.answerMsg);
              this.getTbList();
            } else {
              this.$message.error(res.data.answerMsg);
            }
          });
      });
    }
  },
  mounted() {
    this.getTbList();
  }
};
</script>



